
import {
  BrowserRouter as Router,
  Routes,
  Route,
  useNavigate,
  Link,
  NavLink,

  
} from "react-router-dom";
import './App.css';
import React, { Component } from 'react';
import { Redirect } from 'react-router';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
 

import Bitcoin from './Bitcoin.js'
import Form from './Form.js'
import Register from './Register.js'
import { useEffect, useState } from 'react';
//import { app } from './firebaseConfig';
import { getAuth, signInWithEmailAndPassword, createUserWithEmailAndPassword } from 'firebase/auth'
//import { auth,provider } from "firebase-admin";
import {auth,provider} from "./firebaseConfig";


function App() {
 
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('')
  let navigate = useNavigate();
  const signin = () => {
    auth.signInWithPopup(provider).then((response) => {
navigate("/Bitcoin")
sessionStorage.setItem('Auth Token',true)
      // ...
    }).catch((error) => {
      console.log(error)
     // toast.error("Please check your email and password")
      // ...
    });
}
  const handleAction = (id) => {
    const authentication = getAuth();
    if (id === 2) {
      createUserWithEmailAndPassword(authentication, email, password)
        .then((response) => {
          navigate('/Bitcoin')
          sessionStorage.setItem('Auth Token', true)
      })
      .catch((error) => {
        if (error.code === 'auth/email-already-in-use') {
          toast.error('Email Already in Use');
        }
      })
      
   }

   if (id === 1) {
    signInWithEmailAndPassword(authentication, email, password)
      .then((response) => {
        navigate('/Bitcoin')
        sessionStorage.setItem('Auth Token',true)
      })
      .catch((error) => {
        console.log(error.code)
        if (error.code === 'auth/wrong-password') {
          toast.error('Please check the Password');
        }
        if (error.code === 'auth/user-not-found') {
          toast.error('Please check the Email');
        }
      })

  }
}


 var img;
  return (

   
  <div className="App-header">
   
  
  <h1 >CoinApp</h1>
  <h5>Sign in to know the current bitcoin price</h5>
  
  <img src="https://purepng.com/public/uploads/large/purepng.com-bitcoinobjectsbitcoinmoneycashcoinobjectgoldcurrencypaymentbitcoinvirtual-631522324013ysu8d.png" width="200" height="200"></img>
 
  <ul><Link to="/Register">Sign-up</Link></ul>
  <ul> <Link to="/Login">Sign-in</Link></ul>
 <ul><button style={{"marginTop":"200px", "background-color": "#008CBA", "border-radius": "4px"}} onClick={signin}>Sign in with google</button>

</ul>

  
  
 <Routes>
  <Route path="/Login" element={
                <Form
                  title="Login"
                  setEmail={setEmail}
                  setPassword={setPassword}
                  handleAction={() => handleAction(1)}
                />}></Route>
  <Route path="/Register" element={<Form
                  title="Register"
                  setEmail={setEmail}
                  setPassword={setPassword}
                  handleAction={() => handleAction(2)}
                />} >
    </Route>
    <Route
            path='/Bitcoin'
            element={
              <Bitcoin />}
          />
  </Routes>
  <ToastContainer />
  </div>

  
  
  
    
  );
 
  
}


export default App;
