import * as React from 'react';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import { getAuth, signInWithPopup, GoogleAuthProvider } from "firebase/auth";
import Button from './Button';

import { useNavigate } from 'react-router-dom';
export default function BasicTextFields({ title, setPassword, setEmail, handleAction }) {
    let navigate = useNavigate();
   
    
   
    return (
        <div>
            <div className="heading-container">
                <h3>
                    {title} Form
                </h3>
            </div>
            <div>
            <Box
                component="form"
                $sx={{
                    '& > :not(style)': { m: 1, width: '25ch' },
                }}
                noValidate
                autoComplete="off"
            >
                <div><TextField id="email" label="Enter the Email" variant="outlined" onChange={(e) => setEmail(e.target.value)} /></div>
               <div><TextField id="password" label="Password" variant="outlined"  input type="password" onChange={(e) => setPassword(e.target.value)}/></div>
            </Box>
            </div>

            <Button $title={title} handleAction={handleAction} />
            
        </div>
    );
}